html{
  font-family: Roboto, sans-serif;
}

body {
  background-color: wheat;
  margin: 0;
}

.header{
  padding: 0 20px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav{
  padding: 20px 0;
}

.logo{
  display:block ;
}

.nav_link{
  margin: 15px;
  color: #000;
  text-decoration: none;
}

.nav_link:hover{
  color: #d927e9;
}

.nav_link4{
  text-decoration: none;
  color: #00ACDC;
}

.is-active{
  text-decoration: none;
  position: relative;
  color: #00ACDC;
}

.is-active::after{
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: -20;
  height: 4px;
  background-color: #00ACDC;
}
